  >
    <el-menu-item index="1">
      <router-link id="Home" to="/">
        <i style="font-size:25px;" class="el-icon-menu"></i><i style="font-size:25px;"><b>LibSignal</b></i>
      </router-link>
    </el-menu-item>

    <el-menu-item index="2">
      <router-link id="Directions" to="/Directions">
        <i style="font-size:25px;" class="el-icon-position"></i>Get Started
      </router-link>
    </el-menu-item>

    <el-menu-item index="8">
      <!-- <router-link to="http://114.132.64.138:8080/index.html"> -->
        <i style="font-size:25px;" class="el-icon-download"></i>
      <a class="Link1" href="http://114.132.64.138:8080/content/getstart/Install.html" target="_blank">Installation</a>
