  </div>
</template>

<script>
export default {
  data() {
    return {
      activeIndex: '1'
      // activeIndex2: '1'
    }
  },

  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath)
    }
  }
}
</script>

<style scoped>
a {
  text-decoration: none;
}

.router-link-active {
  text-decoration: none;
}
</style>>



