<template>
  <div class="block">
    <span class="demonstration"></span>
    <el-carousel height= "250px" indicator-position="outside" class="lun_imgs" ref="carousel" @click.native="linkTo">
           <el-carousel-item class="lun_img" v-for="item in imgs" v-bind:key="item.url" >
             <img :src="item.url"/>
           </el-carousel-item>
         </el-carousel>
  </div>

</template>
<script>
export default {
  data: function () {
    return {
      platform: 'pc',
      imgs: [
        {url: require('../../assets/1.jpg'), link: '/assets/1.jpg'},
        {url: require('../../assets/1.jpg'), link: '/assets/1.jpg'},
        {url: require('../../assets/1.jpg'), link: '/assets/1.jpg'},
        {url: require('../../assets/1.jpg'), link: '/assets/1.jpg'}
      ]
    }
  },
  methods: {
    linkTo () {
      let activeIndex = this.$refs.carousel.activeIndex
      this.$router.push(this.imgs[activeIndex].link)
    }
  }
}
</script>>

<style>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }
</style>



